import React from 'react';
import InitFirebase from '../../model/ModelFirebase';

class MainRightProjects extends React.Component {

    constructor(props) {
        super(props);
    }
    render() {
        
        var nameClass = this.props.nameClass;
        return(
            <div class={"card-content" + ( nameClass === "portfolio" ?" active" : "")} id="projects">
                <div class="m-b-3 m-t-2 categorys">
                    <div class="category-show">
                        <span></span>
                        <span>CATEGORY : </span>
                        <span id="category-selected">ALL</span>
                    </div>
                    <div class="category-list">
                        <div class="category-list-wrap">
                        <div class="category-list-inner">
                                <div class="category-button" for="">
                                    <span>ALL</span>
                                </div>
                                <div class="category-button" for="augmented reality">
                                    <span>AR</span>
                                </div>
                                <div class="category-button" for="blender">
                                    <span>Blender 3D</span>
                                </div>
                                <div class="category-button" for="javascript,css">
                                    <span>CSS/Javascript</span>
                                </div>
                                <div class="category-button" for="android">
                                    <span>Mobile</span>
                                </div>
                                <div class="category-button" for="node js,php">
                                    <span>Website</span>
                                </div>
                                <div class="category-button" for="other">
                                    <span>Others</span>
                                </div>
                        </div>
                        </div>
                    </div>
                </div>
                <div id="container-projects">
                    <InitFirebase isDone={this.props.isDone}/>
                </div>
            </div>
        );
    }
}

export default MainRightProjects;